.App {
  text-align: center;
}

.Main {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

html {
  scroll-behavior: smooth;
}


h2 {
  margin-top: calc(50px + 15vmin);
  font-size: calc(10px + 5vmin);
  color: #BF9973;
}


